e === 'vuln' ? 'badge-vuln' : 'badge-secure'\">\r\n              {{ configStore.xssMode.toUpperCase() }} 模式\r\n            </span>\r\n          </div>\r\n        </div>\r\n        \r\n        <div v-if=\"!loading\" class=\"stats-grid\">\r\n          <div class=\"stat-card card\">\r\n            <div class=\"stat-icon\" style=\"background: rgba(34, 211, 238, 0.2)\">\r\n              <el-icon size=\"32\" color=\"#22D3EE\"><View /></el-icon>\r\n            </div>\r\n            <div class=\"stat-info\">\r\n              <div class=\"stat-value\">{{ stats.todayVisits }}</div>\r\n              <div class=\"stat-label\">今日访问</div>\r\n            </div>\r\n          </div>\r\n          \r\n          <div class=\"stat-card card\">\r\n            <div class=\"stat-icon\" style=\"background: rgba(251, 146, 60, 0.2)\">\r\n              <el-icon size=\"32\" color=\"#FB923C\"><UserFilled /></el-icon>\r\n            </div>\r\n            <div class=\"stat-info\">\r\n              <div class=\"stat-value\">{{ stats.newUsers }}</div>\r\n              <div class=\"stat-label\">新增用户</div>\r\n            </div>\r\n          </div>\r\n          \r\n          <div class=\"stat-card card\">\r\n            <div class=\"stat-icon\" style=\"background: rgba(34, 197, 94, 0.2)\">\r\n              <el-icon size=\"32\" color=\"#22C55E\"><Document /></el-icon>\r\n            </div>\r\n            <div class=\"stat-info\">\r\n              <div class=\"stat-value\">{{ stats.totalArticles }}</div>\r\n              <div class=\"stat-label\">文章总数</div>\r\n            </div>\r\n          </div>\r\n          \r\n          <div class=\"stat-card card\">\r\n            <div class=\"stat-icon\" style=\"background: rgba(239, 68, 68, 0.2)\">\r\n              <el-icon size=\"32\" color=\"#EF4444\"><ChatDotRound /></el-icon>\r\n            </div>\r\n            <div class=\"stat-info\">\r\n              <div class=\"stat-value\">{{ stats.pendingFeedbacks }}</div>\r\n              <div class=\"stat-label\">待处理反馈</div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n        \r\n        <div v-else class=\"loading\">\r\n          <div class=\"spinner\"></div>\r\n        </div>\r\n      </div>\r\n    </main>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\nimport { ref, computed, onMounted } from 'vue';\r\nimport { useRoute } from 'vue-router';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport axios from '@/api/axios';\r\n\r\nconst route = useRoute();\r\nconst configStore = useConfigStore();\r\n\r\nconst stats = ref({\r\n  todayVisits: 0,\r\n  newUsers: 0,\r\n  totalArticles: 0,\r\n  pendingFeedbacks: 0\r\n});\r\nconst loading = ref(true);\r\n\r\nconst activeMenu = computed(() => route.path);\r\n\r\nconst fetchStats = async () => {\r\n  try {\r\n    const response = await axios.get('/admin/dashboard');\r\n    stats.value = response.data;\r\n  } catch (error) {\r\n    console.error('Failed to fetch stats:', error);\r\n  } finally {\r\n    loading.value = false;\r\n  }\r\n};\r\n\r\nonMounted(() => {\r\n  fetchStats();\r\n});\r\n</script>\r\n\r\n<style scoped>\r\n.admin-layout {\r\n  display: flex;\r\n  min-height: 100vh;\r\n  background: var(--color-bg-base);\r\n}\r\n\r\n.admin-sidebar {\r\n  width: 250px;\r\n  background: var(--color-bg-card);\r\n  border-right: 1px solid var(--color-border);\r\n  padding: var(--spacing-lg);\r\n  position: fixed;\r\n  height: 100vh;\r\n  overflow-y: auto;\r\n}\r\n\r\n.admin-brand {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-md);\r\n  padding: var(--spacing-lg);\r\n  margin-bottom: var(--spacing-xl);\r\n  color: var(--color-primary);\r\n}\r\n\r\n.admin-brand h2 {\r\n  font-size: 1.25rem;\r\n}\r\n\r\n.admin-main {\r\n  flex: 1;\r\n  margin-left: 250px;\r\n  padding: var(--spacing-2xl);\r\n}\r\n\r\n.admin-content {\r\n  max-width: 1400px;\r\n  margin: 0 auto;\r\n}\r\n\r\n.dashboard-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  margin-bottom: var(--spacing-2xl);\r\n}\r\n\r\n.dashboard-header h1 {\r\n  font-size: 2.5rem;\r\n}\r\n\r\n.stats-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));\r\n  gap: var(--spacing-lg);\r\n}\r\n\r\n.stat-card {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-lg);\r\n  padding: var(--spacing-xl);\r\n}\r\n\r\n.stat-card:hover {\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.stat-icon {\r\n  width: 64px;\r\n  height: 64px;\r\n  border-radius: var(--radius-md);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n}\r\n\r\n.stat-value {\r\n  font-size: 2rem;\r\n  font-weight: 700;\r\n  color: var(--color-text-primary);\r\n  margin-bottom: 4px;\r\n}\r\n\r\n.stat-label {\r\n  color: var(--color-text-muted);\r\n  font-size: 0.9rem;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
